import React, { Component } from 'react';
import './App.css';
import Rvcserevices8 from './Rvcserevices8';
import btn_icon_back_rvcservices from './images/btn_icon_back_rvcservices.png';

// UI framework component imports
import Appbar from 'muicss/lib/react/appbar';

export default class RVCServicesScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
    {
      let dataSheet = this.props.appActions.dataSheets['rvcservices'];
      let serviceOptions = this.props.appActions.serviceOptions_rvcservices;
      if ( !this.props.appActions.dataSheetLoaded['rvcservices']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-rvcservices", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet10(dataSheet, serviceOptions, true);
        this.props.appActions.dataSheetLoaded['rvcservices'] = true;
      }
      this._dataSheetLoadTimer_rvcservices = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-rvcservices", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet10(dataSheet, serviceOptions, false);
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this._dataSheetLoadTimer_rvcservices);
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const dataSheet_rvcservices = this.props.dataSheets['rvcservices'];
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    const style_elText = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    let filterItems_list = items => {
      // This function filters items for the List / Grid element.
      // There is a variable named 'items' that provides item values.
      return items;
    
    }
    items_list = items_list.concat(filterItems_list(this.props.appActions.getDataSheet('rvcservices').items));
    
    const style_elList = {
      height: 'auto',  // This element is in scroll flow
     };
    const style_elTextCopy3 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    const style_elTextCopy3_outer = {
      display: 'none',
     };
    const style_elTextCopy6 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    const style_elTextCopy6_outer = {
      display: 'none',
     };
    const style_elTextCopy7 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    const style_elTextCopy7_outer = {
      display: 'none',
     };
    const style_elTextCopy8 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    const style_elTextCopy8_outer = {
      display: 'none',
     };
    const style_elTextCopy4 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    const style_elTextCopy4_outer = {
      display: 'none',
     };
    const style_elTextCopy5 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    const style_elTextCopy5_outer = {
      display: 'none',
     };
    
    return (
      <div className="AppScreen RVCServicesScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elText">
            <div className="systemFontBold" style={style_elText}>
              <div>{this.props.locStrings.apps2_text_541757}</div>
            </div>
          </div>
          
          <div className="hasNestedComps elList">
            <ul style={style_elList}>
              {items_list.map((row, index) => {
                let itemComp = (row._componentId)
                    ? listComps_list[row._componentId]
                    : <Rvcserevices8 dataSheetId={'rvcservices'} dataSheetRow={row} {...{ 'url': row['url'], 'name': row['name'], }} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />;
                return (<li key={row.key}>
                    {itemComp}
                  </li>);
              })}
              <div className="marker" ref={(el)=> this._elList_endMarker = el} />
            </ul>
          </div>
          
          <div className="elTextCopy3" style={style_elTextCopy3_outer}>
            <div className="systemFontBold" style={style_elTextCopy3}>
              <div>{this.props.locStrings.apps2_textcopy3_530665}</div>
            </div>
          </div>
          
          <div className="elTextCopy6" style={style_elTextCopy6_outer}>
            <div className="systemFontBold" style={style_elTextCopy6}>
              <div>{this.props.locStrings.apps2_textcopy6_477079}</div>
            </div>
          </div>
          
          <div className="elTextCopy7" style={style_elTextCopy7_outer}>
            <div className="systemFontBold" style={style_elTextCopy7}>
              <div>{this.props.locStrings.apps2_textcopy7_1041141}</div>
            </div>
          </div>
          
          <div className="elTextCopy8" style={style_elTextCopy8_outer}>
            <div className="systemFontBold" style={style_elTextCopy8}>
              <div>{this.props.locStrings.apps2_textcopy8_137669}</div>
            </div>
          </div>
          
          <div className="elTextCopy4" style={style_elTextCopy4_outer}>
            <div className="systemFontBold" style={style_elTextCopy4}>
              <div>{this.props.locStrings.apps2_textcopy4_327093}</div>
            </div>
          </div>
          
          <div className="elTextCopy5" style={style_elTextCopy5_outer}>
            <div className="systemFontBold" style={style_elTextCopy5}>
              <div>{this.props.locStrings.apps2_textcopy5_593088}</div>
            </div>
          </div>
        </div>
        <Appbar className="navBar">
          <div className="title">- RVC Services -</div>  <div className="backBtn" onClick={ (ev)=>{ this.props.appActions.goBack() } }><img src={btn_icon_back_rvcservices} alt="" style={{width: '50%'}} /></div>
        </Appbar>
        
      </div>
    )
  }
  
}
