import React, { Component } from 'react';
import './App.css';
import Appannouncemts from './Appannouncemts';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class StartScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo, announcement

  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
    {
      let dataSheet = this.props.appActions.dataSheets['appannouncement'];
      let serviceOptions = this.props.appActions.serviceOptions_appannouncement;
      if ( !this.props.appActions.dataSheetLoaded['appannouncement']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-appannouncement", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet1(dataSheet, serviceOptions, true);
        this.props.appActions.dataSheetLoaded['appannouncement'] = true;
      }
      this._dataSheetLoadTimer_appannouncement = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-appannouncement", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet1(dataSheet, serviceOptions, false);
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this._dataSheetLoadTimer_appannouncement);
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  onClick_elButtonCopy = (ev) => {
    // Go to screen '- Eagle -'
    this.props.appActions.goToScreen('eagle', { transitionId: 'fadeIn' });
  
  }
  
  
  onClick_elButtonCopy2 = (ev) => {
    // Go to screen '- Apps -'
    this.props.appActions.goToScreen('apps', { transitionId: 'fadeIn' });
  
  }
  
  
  onClick_elButtonCopy3 = (ev) => {
    // Go to screen '- RVC Services -'
    this.props.appActions.goToScreen('rvcservices', { transitionId: 'fadeIn' });
  
  }
  
  
  onClick_elButton = (ev) => {
    // Go to screen '- Chat -'
    this.props.appActions.goToScreen('chat', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const dataSheet_appannouncement = this.props.dataSheets['appannouncement'];
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    const style_elTitle = {
      fontSize: 22.8,
      color: '#1436ff',
      textAlign: 'center',
     };
    const style_elButtonCopy = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elButtonCopy2 = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elButtonCopy3 = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elButton = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elText = {
      fontSize: 12.3,
      color: 'rgba(0, 0, 0, 0.5000)',
      textAlign: 'center',
     };
    const style_elLabel = {
      color: '#009192',
      textAlign: 'center',
     };
    
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    items_list = items_list.concat(this.props.appActions.getDataSheet('appannouncement').items);
    
    const style_elList = {
      height: 'auto',  // This element is in scroll flow
     };
    
    return (
      <div className="AppScreen StartScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elTitle">
            <div className="systemFontBold" style={style_elTitle}>
              <div><div dangerouslySetInnerHTML={{__html: this.props.locStrings.start_text_473557.replace(/\n/g, '<br>')}}></div></div>
            </div>
          </div>
          
          <div className="elButtonCopy">
            <Button className="actionFont" style={style_elButtonCopy}  color="accent" onClick={this.onClick_elButtonCopy} >
              {this.props.locStrings.start_buttoncopy_85131}
            </Button>
          </div>
          
          <div className="elButtonCopy2">
            <Button className="actionFont" style={style_elButtonCopy2}  color="accent" onClick={this.onClick_elButtonCopy2} >
              {this.props.locStrings.start_buttoncopy2_944903}
            </Button>
          </div>
          
          <div className="elButtonCopy3">
            <Button className="actionFont" style={style_elButtonCopy3}  color="accent" onClick={this.onClick_elButtonCopy3} >
              {this.props.locStrings.start_buttoncopy3_498089}
            </Button>
          </div>
          
          <div className="elButton">
            <Button className="actionFont" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.props.locStrings.start_button_749629}
            </Button>
          </div>
          
          <div className="elText">
            <div className="systemFontRegular" style={style_elText}>
              <div><div dangerouslySetInnerHTML={{__html: this.props.locStrings.start_text_91983.replace(/\n/g, '<br>')}}></div></div>
            </div>
          </div>
          
          <div className="elLabel">
            <div className="baseFont" style={style_elLabel}>
              <div>{this.props.locStrings.start_text3_814591}</div>
            </div>
          </div>
          
          <div className="hasNestedComps elList">
            <ul style={style_elList}>
              {items_list.map((row, index) => {
                let itemComp = (row._componentId)
                    ? listComps_list[row._componentId]
                    : <Appannouncemts dataSheetId={'appannouncement'} dataSheetRow={row} {...{ 'datetime': row['datetime'], 'user': row['user'], 'announcement': row['announcement'], }} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />;
                return (<li key={row.key}>
                    {itemComp}
                  </li>);
              })}
              <div className="marker" ref={(el)=> this._elList_endMarker = el} />
            </ul>
          </div>
        </div>
        
      </div>
    )
  }
  
}
